<template>
  <div class="page-container">
    <div class="dashboard-text">概况 {{ userName }}</div>
    <div ref="chart" class="echart" />
  </div>
</template>

<script>
import { getList } from '@/api/kg'
import { mapGetters } from 'vuex'
import graph from '@/assets/data/les-miserables.json'

export default {
  name: 'Dashboard',
  data() {
    return {
      ROOT_PATH: 'https://cdn.jsdelivr.net/gh/apache/echarts-website@asf-site/examples',
      // ROOT_PATH: 'https://github.com/apache/echarts-website/tree/asf-site/examples'
      kg_data: null
    }
  },
  computed: {
    ...mapGetters({
      userId: 'user/userId',
      userName: 'user/userName',
      mobilePhone: 'user/mobilePhone',
      email: 'user/email',
      ugroup: 'user/ugroup',
      cid: 'user/cid',
      uscc: 'user/uscc'
    })
  },
  mounted() {
    // this.bar3d();
    // this.pie();
    // this.map();
    // this.miserables()
  },
  created() {
    this.fetchData()
  },
  methods: {
    fetchData() {
      this.loading = true
      getList()
        .then((response) => {
          const { status, data } = response.data
          if (status == 200) {
            this.kg_data = data
            this.coffeeKG()
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    coffeeKG() {
      const charts = this.$Echarts.init(this.$refs.chart)
      this.kg_data.links.forEach(function(link) {
        link.label = {
          show: true,
          position: 'middle',
          formatter: '{c}'
        }
      })
      var option = {
        title: {
          text: 'Coffee',
          subtext: 'Default layout',
          top: 'bottom',
          left: 'right'
        },
        animationDuration: 1500,
        animationEasingUpdate: 'quinticInOut',
        series: [
          {
            name: 'Coffee',
            type: 'graph',
            layout: 'force',
            data: this.kg_data.data,
            links: this.kg_data.links,
            // categories: graph.categories,
            roam: true,
            label: {
              show: true,
              position: 'right',
              formatter: '{b}'
            },
            labelLayout: {
              hideOverlap: true
            },
            scaleLimit: {
              min: 0.4,
              max: 2
            },
            lineStyle: {
              color: 'source',
              curveness: 0.3
            },
            emphasis: {
              focus: 'adjacency',
              lineStyle: {
                width: 10
              }
            }
          }
        ]
      }
      charts.setOption(option)
    },

    miserables() {
      const charts = this.$Echarts.init(this.$refs.chart)
      // console.log("getJSON: ", graph)
      graph.nodes.forEach(function(node) {
        node.label = {
          show: node.symbolSize > 30
        }
      })
      var option = {
        title: {
          text: 'Les Miserables',
          subtext: 'Default layout',
          top: 'bottom',
          left: 'right'
        },
        tooltip: {},
        legend: [{
          // selectedMode: 'single',
          data: graph.categories.map(function(a) {
            return a.name
          })
        }],
        animationDuration: 1500,
        animationEasingUpdate: 'quinticInOut',
        series: [
          {
            name: 'Les Miserables',
            type: 'graph',
            layout: 'none',
            data: graph.nodes,
            links: graph.links,
            categories: graph.categories,
            roam: true,
            label: {
              show: true,
              position: 'right',
              formatter: '{b}'
            },
            labelLayout: {
              hideOverlap: true
            },
            scaleLimit: {
              min: 0.4,
              max: 2
            },
            lineStyle: {
              color: 'source',
              curveness: 0.3
            },
            emphasis: {
              focus: 'adjacency',
              lineStyle: {
                width: 10
              }
            }
          }
        ]
      }
      charts.setOption(option)
    }
  }
}
</script>

<style lang="scss" scoped>
.dashboard {
  &-text {
    font-size: 18px;
    line-height: 32px;
  }
}
.echart {
  left: 0;
  top: 0;
  width: 100%;
  height: 500px;
  // background-color: gold;
}
</style>
